@import './color.scss';
@import './font-style.scss';

.form-register input {
  background-size: 1.5rem 1.5rem;
  background-repeat: no-repeat;
  background-position: 1rem 0.75rem;
  padding: 1.5rem 4rem;
}

.form-register:nth-child(1)>input {
  background-image: url("../icons/Form/register-mail.svg");
}

.form-register:nth-child(2)>input {
  background-image: url("../icons/Form/register-user.svg");
}

.form-register:nth-child(3) > input {
  height:50px;
}

.eye-container {
  position: absolute;
}

.form-register:nth-child(3)>input {
  background-image: url("../icons/Form/register-lock.svg");
}

.form-register:nth-child(4)>input {
  background-image: url("../icons/Form/register-phone.svg");
}

.register-eye {
  display:flex;
  justify-self:flex-start;
  position: relative;
  top: -3.75rem;
  right: -22rem;
}

.App {
  text-align: center;
  min-height: 100vh;
}

html {
  overflow-y: scroll;
}

.hidden {
  display:none !important;
  transition: all 1s ease;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin) !important;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg) !important;
  }

  to {
    transform: rotate(360deg) !important;
  }
}


@media only screen and (max-width:1199px){
  .register-eye {
    right: -14.5rem;
  }
}
@media only screen and (max-width:991px) {
  .LoginSideImage {
    display: none !important;
    flex: 0 0 0 !important;
  }

  .register-eye {
    right: -19.5rem;
  }
}

@media only screen and (max-width:767px) {
  .register-section{
    padding:0 !important;
  }

  .register-form {
    padding:0 !important;
  }

  .register-eye {
    right: -20.5rem;
  }
}
@media only screen and (max-width:575px){
  .register-eye {
    right: -88%;
  }
}

@media only screen and (max-width:500px) {
  .register-nav > .nav-item > *{
    padding: 0 .5rem;
  }
}

@media only screen and (max-width:468px) {
  .register-nav > .nav-item > *{
    padding: 0 .5rem;
  }

  .register-form {
    margin:0 !important;
    padding:0 !important;
  }

  .form-register input {
    background-size: 1.5rem 1.5rem;
    background-repeat: no-repeat;
    background-position: 1rem 0.75rem;
    padding: 1.5rem 0 1.5rem 4rem ;
  }
}

@media only screen and (max-width:433px) {
  .register-nav > .nav-item > *{
    padding: 0 .2rem;
    font-size: 12px !important;
  }

  .register-nav > .nav-item > * > button{
    padding: .5rem .75rem !important;
    font-size: 12px !important;
  }
}